:root {
  --clr-primary-700: hsl(300, 43%, 22%);
  --clr-primary-300: hsl(333, 80%, 67%);
  --ff-primary: "Poppins", monospace;
  --fs-400: .8125rem;
  --fs-500: .9725rem;
  --fs-900: 1.3rem;
  --fs-1300: 2.5rem;
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
body {
  font-family: var(--ff-primary);
  font-size: var(--fs-400);
  background: #26272c;
  color: white;
}

h1 {
  font-size: var(--fs-1300);
  line-height: 1;
  letter-spacing: 0.1rem;
}

ul {
  list-style: none;
  padding: 0;
  margin-right: auto;
}

a {
  text-decoration: none;
  color: white;
}

li a {
  margin-right: 2rem;
}

img {
  width: 100%;
  clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
}

ul.transition {
  display: flex;
  position: absolute;
  z-index: 10;
  height: 100vh;
  width: 100%;
  top: 0;
  left: 0;
  margin: 0;
  pointer-events: none;
}

ul.transition li {
  transform: scaleY(0);
  background: white;
  width: 20%;
}

.chouky-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: -200;
  background-image: linear-gradient(to bottom, #072142, #061c37, #07182b, #061220, #020b16);
}

header {
  padding: 20px 6.25%;
  position: absolute;
  top: 0;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  z-index: 1000;
}

.header__name {
  font-size: var(--fs-500);
  letter-spacing: 0.1rem;
}

.header__nav {
  display: none;
}

.fullpage__slide {
  width: 100%;
  height: 100vh;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  margin: auto;
}

.title {
  position: absolute;
  overflow: hidden;
  width: 87.5%;
  margin: 0 6.25%;
  top: auto;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  bottom: 8em;
  z-index: 200;
}
.title__text {
  font-size: var(--fs-1300);
  line-height: 1;
  letter-spacing: 0.1em;
  margin-bottom: 0.8rem;
}
.title__border {
  display: inline-block;
  margin-bottom: 0.8rem;
}
.title__border span:first-child {
  margin-bottom: 0.7rem;
}
.title__border span:last-child {
  margin-left: 4em;
}
.title__border span {
  display: block;
  width: 100px;
  height: 4px;
  border-radius: 5px;
  background: #534695;
}
.title__lead {
  color: #fff;
  font-size: var(--fs-900);
  line-height: 1.4;
  letter-spacing: 0.1em;
}

.image {
  width: 80%;
  height: 55.633%;
  margin: 0 10%;
  top: 9.859%;
  display: block;
  position: relative;
  box-shadow: 0 80px 140px -40px rgba(0, 0, 0, 0.6);
}
.image--works {
  background: url(/src/images/motm.jpg) no-repeat center;
  background-size: cover;
}
.image--about {
  background: url(/src/images/about-me.jpg) no-repeat center;
  background-size: cover;
}

.fp-prev {
  margin-left: 2rem;
}

.fp-next {
  margin-right: 2rem;
}

#fp-nav ul li a.active span {
  height: 16px !important;
  width: 16px !important;
  background: url(/src/images/dragon_ball.svg) no-repeat center;
}

@media (min-width: 768px) {
  .fullpage__slide {
    display: flex;
    align-items: center;
    position: relative;
  }

  .title {
    width: 100%;
    top: 50%;
    -webkit-transform: translate3d(0, -50%, 0);
    transform: translate3d(0, -50%, 0);
    margin: 0 0 0 11%;
    bottom: 0;
  }

  .image {
    width: 53.125%;
    height: 74.81%;
    margin: 0 8% 0 auto;
    box-shadow: 0 80px 140px -40px rgba(0, 0, 0, 0.6);
    top: 0;
  }
}
@media (min-width: 992px) {
  :root {
    --fs-900: 2rem;
    --fs-1300: 8rem;
  }
}

